#userlist {
	font-weight: bold;
	h3 {
		color: #FFA500;
		font-weight: bolder;
		font-size: 25px;
		text-shadow: 1px 1px #000;
		padding: 10px; }
	table {
		text-align: center;
		padding: 10px;
		th {
			font-weight: bolder;
			font-size: 18px;
			text-shadow: 1px 1px #E52D2D; }
		td {
			padding: 7px 5px;
			font-size: 1.1em; } }

	@media screen and (max-width: 768px) {
		table {
			width: 100%;
			thead {
				display: none;
				tr:nth-of-type(2n) {
					background-color: inherit; }
				tr td:first-child {
					background: #f0f0f0;
					font-weight: bold;
					font-size: 1.3em; } }
			tbody {
				td {
					display: block;
					text-align: center; } }
			tbody {
				td:before {
				    content: attr(data-th);
				    display: block;
				    text-align: center; } } } } }
